import React, { useState } from 'react';
import { Nav } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

const Product = (props) => {
  let navigate = useNavigate();
  let { gagu, i, topRate, discount } = props;
  const price = gagu.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  const price1 = (gagu.price * 0.9).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")



  return (
    // <Col md={3}>
    <Nav.Link onClick={() =>
      navigate('/detail/' + gagu.id)}>


      {topRate && <h6>Top&nbsp;{i + 1}</h6>}
      <img src={process.env.PUBLIC_URL + '/' + gagu.imgUrl[0]} width="100%" alt="item" />
      <h4>{gagu.title}</h4>
      <p className={discount ? "noprice" : null}>&#8361;{price}</p>
      {discount ? <h6 className='dcprice'>&#8361;{price1}</h6> : null}
    </Nav.Link>
    // </Col>
  )
}

export default Product