import React from 'react'
import '../contact.css';
import { Col, Container, Row, Form, Button,InputGroup } from 'react-bootstrap';

const Contact = () => {
  return (
    <>
      <div className='contacttop'>
        <h2>Contact Us</h2>
        <Container>
          <Row className='contactbox'>
            <Col md={6} className='left'>
              {/* <img src={process.env.PUBLIC_URL + '/img/contact.jpg'} /> */}
            </Col>
            <Col md={6} className='right'>
              <h4>Welcome to CASAFORM</h4>
              <p>We make it easy for everyone to Living Life</p>
              <Form>
                <Form.Group as={Row} className="mb-3" controlId="formPlaintextEmail">
                  <Form.Label column sm="2">
                    email
                  </Form.Label>
                  <Col sm="10">
                    {/* <Form.Control type="email" placeholder="id@casaform.com" /> */}
                    <InputGroup className="mb-3">
                      <Form.Control
                        placeholder="id"
                        aria-label="id"
                        aria-describedby="basic-addon2"
                      />
                      <InputGroup.Text id="basic-addon2">@casaform.com</InputGroup.Text>
                    </InputGroup>
                  </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" controlId="formPlaintextPassword">
                  <Form.Label column sm="2">
                    Password
                  </Form.Label>
                  <Col sm="10">
                    <Form.Control type="password" placeholder="Password" />
                  </Col>
                </Form.Group>
              </Form>
              <Row>
                <Col md={2}></Col>
                <Col md={5}>
                  <div className="d-grid gap-2">
                    <Button className="contactbtn">
                      Login
                    </Button>
                  </div>
                </Col>
                <Col md={5}>
                  <div className="d-grid gap-2">
                    <Button className="contactbtn">
                      Create
                    </Button>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>


    </>
  )
}

export default Contact